/* Ghost */
/* Bookmark card */
/* stylelint-disable-next-line selector-no-qualifying-type */
.kg-bookmark-card a.kg-bookmark-container {
  border-color: var(--border-color);
  border-radius: var(--radius);
  transition: border-color 0.3s;
}

.kg-bookmark-card .kg-bookmark-icon {
  margin-inline: 0 6px;
}

.kg-bookmark-title {
  font-size: var(--small) !important;
}

.kg-bookmark-content {
  padding: 1rem;
}

.kg-bookmark-description {
  font-size: var(--small) !important;
  color: var(--element-light);
}

.kg-bookmark-metadata {
  font-size: var(--small) !important;
}

/* stylelint-disable-next-line selector-no-qualifying-type */
.kg-bookmark-card:hover a.kg-bookmark-container {
  border-color: var(--primary-light);
  border-radius: var(--radius);
}

@media only screen and (max-width: 768px) {
  .kg-bookmark-card .kg-bookmark-thumbnail {
    display: none;
    transition: var(--transition);
  }
  .kg-bookmark-card .kg-bookmark-description {
    display: none;
    transition: var(--transition);
  }
}

/* Toggle card */
.kg-toggle-card {
  padding: 1rem;
  border: 1px solid var(--border-color);
  border-radius: var(--radius);
  box-shadow: none;
}

.kg-toggle-heading svg {
  color: var(--primary-light);
}

.kg-toggle-card:hover .kg-toggle-heading svg {
  color: var(--primary);
}

/* Button */
.kg-button-card {
  font-family: inherit;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: var(--radius);
}

.kg-btn {
  height: initial;
  padding: var(--button-padding);
  border-radius: var(--radius);
}

.kg-btn-accent {
  color: var(--button-text);
  background-color: var(--primary);
  transition: background-color 0.3s;
}

.kg-btn-accent:hover {
  background-color: var(--primary-light);
  opacity: 1;
}

/* Callout card */
.kg-callout-card {
  padding: 1rem;
  background-color: var(--border-color);
  border-radius: var(--radius);
}

/* Blockquote */
blockquote:not(.kg-blockquote-alt) {
  padding-inline-start: 2rem;
  border-left: 3px solid var(--border-color);
}

.kg-blockquote-alt {
  padding: 0;
  margin-block: 3rem 4rem;
  font-size: 3rem;
  font-weight: 800;
  line-height: 1.2;
  color: var(--element-light);
}

.kg-blockquote-alt::before,
.kg-blockquote-alt::after {
  color: var(--primary);
}

.kg-blockquote-alt::before {
  content: open-quote;
}

.kg-blockquote-alt::after {
  content: close-quote;
}

/* Cards and images */
.kg-card {
  width: 100%;
  margin-inline: auto;
}

.kg-card,
.kg-blockquote-alt {
  margin-block: 1rem 2rem;
}

.kg-image,
.entry-content img {
  display: block;
  max-width: 100%;
  height: auto;
  margin-inline: auto;
}

.entry-content figcaption {
  padding: 0.25rem;
  font-size: var(--small);
  text-align: center;
  color: var(--element-light);
}

.kg-width-wide,
.kg-embed-card,
.kg-gallery-card {
  grid-column: content;
  min-width: 80%;
}

.kg-embed-card .twitter-tweet {
  margin-inline: auto;
}

@media (--tablet) {
  .kg-width-wide,
  .kg-gallery-card {
    grid-column: wide;
  }
}
.kg-width-full {
  grid-column: start/end;
  min-width: 100%;
}

.kg-embed-card [src*=youtube],
.kg-embed-card [src*=vimeo] {
  width: 100%;
  height: auto;
  aspect-ratio: 16/9;
}

.kg-embed-card iframe {
  /* stylelint-disable-next-line declaration-no-important */
  margin-inline: auto !important;
}

.kg-header-card.kg-size-small h2.kg-header-card-header {
  font-size: var(--h2) !important;
}

.kg-header-card.kg-size-small h3.kg-header-card-header {
  font-size: var(--h3) !important;
}

.kg-header-card.kg-size-small .kg-header-card-header:before {
  display: none;
}